}

/* Ensure nav is always visible */
body.page-active-picks .brand-nav {
  z-index: 1000;
  background: transparent;
  display: flex;
  list-style: none;
  gap: 1.5rem; /* Increased but controlled gap */
  align-items: center;
  margin: 0;
  padding: 0;
