}

/* Navigation Link Styles - Premium Serif Typography */
.nav-link {
  font-family:
    "Montserrat", "Inter", sans-serif; /* Switched to cleaner Sans for UI */
  font-weight: 500;
  font-size: 0.65rem; /* Sleeker, smaller size */
  text-transform: uppercase;
  letter-spacing: 0.15em; /* Elegant track */
  color: rgba(255, 255, 255, 0.7); /* Slightly muted for premium feel */
  text-decoration: none;
  padding: 0; /* Removed padding to tighten gap to underline */
  height: 32px; /* Fixed height for consistent vertical alignment */
  display: inline-flex;
  align-items: center;
  cursor: pointer;
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  position: relative;
  background: transparent !important;
  border: none;
  opacity: 1;
  outline: none;
}

/* Hover & Active States */
.nav-link:hover {
  color: #ffffff;
  text-shadow: 0 0 10px rgba(255, 255, 255, 0.3);
}

.nav-link[aria-current="page"] {
  color: #ffffff;
  font-weight: 600;
}

.nav-link[aria-current="page"]::after {
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 1px;
  background: linear-gradient(90deg, transparent, #00ff9d, transparent);
  box-shadow: 0 1px 4px rgba(0, 255, 157, 0.5);
}

/* Disabled / placeholder nav links */
.nav-link[aria-disabled="true"] {
  pointer-events: none; /* Prevent clicks, but look normal */
  /* No opacity or cursor changes - looks like regular link for design */
  color: #e8f0f2; /* Slightly softer white but still readable */
  opacity: 0.85; /* Increased opacity for better readability */
  text-shadow:
    0 1px 3px rgba(0, 0, 0, 0.9),
    0 0 2px rgba(0, 0, 0, 0.7);
}

.nav-link[aria-disabled="true"]::before {
