  transform: translateY(-8px) scale(0.98);
  transition:
    opacity 0.2s cubic-bezier(0.2, 0.8, 0.2, 1),
    transform 0.2s cubic-bezier(0.2, 0.8, 0.2, 1),
    visibility 0s linear 0.2s;
  pointer-events: none;
}

/* ===== Sleek Fetch Button at Sportsbook Level ===== */
.sportsbook-actions-row {
  display: flex !important;
  align-items: center;
  gap: 3px;
  width: 100%;
  margin-top: 2px;
}

.sportsbook-credentials-group {
  display: flex;
  gap: 2px;
  flex: 1;
}

/* ===== Sportsbook Cards ===== */
.sportsbook-card {
  padding: 5px 8px;
  margin-bottom: 2px;
  background: linear-gradient(
    140deg,
    rgba(8, 17, 33, 0.88),
    rgba(6, 22, 26, 0.92)
  );
  border: 1px solid rgba(255, 255, 255, 0.04);
  border-left: 2px solid rgba(0, 143, 88, 0.4);
  border-radius: 4px;
  transition: all 0.2s ease;
  position: relative;
  font-family: var(--sportsbook-body-font);
  letter-spacing: 0.02em;
  line-height: 1.3;
  cursor: pointer;
}

/* Selected state - shows this sportsbook is active for new picks */
.sportsbook-card.selected {
  background: linear-gradient(
    140deg,
    rgba(0, 143, 88, 0.25),
    rgba(0, 100, 60, 0.28)
  );
  border: 1px solid rgba(0, 255, 170, 0.5);
  border-left: 3px solid #00ffaa;
  box-shadow:
    0 0 20px rgba(0, 255, 170, 0.25),
    inset 0 0 10px rgba(0, 255, 170, 0.08);
}

.sportsbook-card.selected .sportsbook-name {
  color: #00ffaa;
  text-shadow: 0 0 12px rgba(0, 255, 170, 0.6);
}

.sportsbook-card.selected::after {
  content: "✓";
  position: absolute;
  top: 4px;
  right: 6px;
  font-weight: 700;
  color: #00ffaa;
  font-size: 0.8rem;
  text-shadow: 0 0 8px rgba(0, 255, 170, 0.6);
}

.sportsbook-card:hover {
  background: linear-gradient(
    140deg,
    rgba(10, 21, 40, 0.9),
    rgba(7, 24, 29, 0.95)
  );
  border-color: rgba(var(--color-primary-bright-rgb), 0.15);
  border-left-color: #00b06c;
  transform: translateY(-1px);
  box-shadow: 0 3px 10px rgba(0, 0, 0, 0.2);
}

/* Success state after fetch for sleek button */
.sportsbook-card.fetch-success {
  border-left-color: #00ffaa;
  animation: successFlash 0.6s ease-out;
}

@keyframes successFlash {
  0% {
    background: linear-gradient(
      140deg,
      rgba(0, 255, 170, 0.3),
      rgba(var(--color-primary-bright-rgb), 0.2)
    );
    box-shadow: 0 0 30px rgba(0, 255, 170, 0.5);
  }
  100% {
    background: linear-gradient(
      140deg,
      rgba(8, 17, 33, 0.85),
      rgba(6, 22, 26, 0.9)
    );
    box-shadow: none;
  }
}

.sportsbook-card.fetch-success .sportsbook-fetch-compact {
  color: #00ffaa;
  background: rgba(0, 255, 170, 0.15);
}

/* Error state */
.sportsbook-card.fetch-error {
  border-left-color: #ff5570;
}

.sportsbook-card.fetch-error .sportsbook-fetch-compact {
  color: #ff5570;
  background: rgba(255, 71, 87, 0.15);
}

.sportsbook-card:focus-within {
  border-color: rgba(var(--color-primary-bright-rgb), 0.25);
  box-shadow: 0 0 0 1px rgba(var(--color-primary-bright-rgb), 0.15);
}

.sportsbook-card-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 4px;
  margin-bottom: 2px;
}

.sportsbook-meta-compact {
  display: flex;
  align-items: center;
  justify-content: flex-end;
  flex-shrink: 0;
}

.sportsbook-name-group {
  display: flex;
  align-items: center;
  gap: 4px;
  flex: 1;
  min-width: 0; /* Allow shrinking */
}

.sportsbook-name {
  font-family: var(--sportsbook-heading-font);
  font-weight: 700;
  font-size: var(--sportsbook-title-size);
  color: #e8f0f2;
  text-transform: uppercase;
  letter-spacing: 0.08em;
  line-height: 1;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

/* Connection Status Indicator */
.connection-status {
  width: 5px;
  height: 5px;
  border-radius: 50%;
  display: inline-block;
  position: relative;
  flex-shrink: 0;
}

.connection-status.connected {
  background: #00ffaa;
  box-shadow: 0 0 8px rgba(0, 255, 170, 0.6);
  animation: statusPulse 2s ease-in-out infinite;
}

@keyframes statusPulse {
  0%,
  100% {
    opacity: 1;
  }
  50% {
    opacity: 0.6;
  }
}

.connection-status.disconnected {
  background: #ff5570;
  box-shadow: 0 0 6px rgba(255, 71, 87, 0.4);
}

.connection-status.syncing {
  background: #80deea;
  box-shadow: 0 0 10px rgba(0, 188, 212, 0.8);
  animation: statusBlink 1s ease-in-out infinite;
}

@keyframes statusBlink {
  0%,
  100% {
    opacity: 1;
    transform: scale(1);
  }
  50% {
    opacity: 0.3;
    transform: scale(1.2);
  }
}

.sportsbook-last-sync {
  font-family: var(--sportsbook-heading-font);
  font-size: var(--sportsbook-caption-size);
  color: #90a4ae;
  font-weight: 500;
  letter-spacing: 0.04em;
  text-transform: uppercase;
  white-space: nowrap;
}

.sync-time {
  color: #b0bec5;
  font-family: var(--sportsbook-heading-font);
  font-weight: 600;
}

.sportsbook-fetch-compact {
  background: transparent;
  border: none;
  padding: 2px 4px;
  margin-left: 4px;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  font-family: var(--sportsbook-heading-font);
  font-size: var(--sportsbook-caption-size);
  font-weight: 600;
  color: #00d689;
  cursor: pointer;
  transition:
    color 0.2s ease,
    transform 0.2s ease,
    background 0.2s ease;
  border-radius: 3px;
  flex-shrink: 0;
}

.sportsbook-fetch-compact .fetch-icon {
  font-size: 0.65rem;
  line-height: 1;
  transition: transform 0.3s ease;
}

.sportsbook-fetch-compact:hover,
.sportsbook-fetch-compact:focus-visible {
  color: #00ffaa;
  background: rgba(var(--color-primary-bright-rgb), 0.12);
  outline: none;
}

.sportsbook-fetch-compact:hover .fetch-icon,
.sportsbook-fetch-compact:focus-visible .fetch-icon {
  transform: rotate(360deg);
}

/* Credentials row */
.sportsbook-credentials-row {
  display: flex;
  align-items: center;
  gap: 3px;
}

.credential-input-compact {
  flex: 1;
  min-width: 0;
  background: rgba(0, 0, 0, 0.2);
  border: 1px solid rgba(255, 255, 255, 0.06);
  color: #cfd8dc;
  font-family: var(--sportsbook-heading-font);
  font-size: var(--sportsbook-label-size);
  font-weight: 500;
  letter-spacing: 0.06em;
  text-transform: uppercase;
  padding: 3px 5px;
  border-radius: 3px;
  transition: all 0.2s ease;
}

.credential-input-compact:focus {
  outline: none;
  border-color: rgba(var(--color-primary-bright-rgb), 0.4);
  background: rgba(0, 0, 0, 0.35);
  box-shadow: 0 0 0 1px rgba(var(--color-primary-bright-rgb), 0.2);
}

.credential-input-compact::placeholder {
  color: #78909c;
  font-family: var(--sportsbook-heading-font);
  font-size: var(--sportsbook-label-size);
  font-weight: 500;
  letter-spacing: 0.06em;
  text-transform: uppercase;
}

/* Syncing State */
.sportsbook-card.syncing {
  border-left-color: #00ffaa;
  background: linear-gradient(
    140deg,
    rgba(0, 143, 88, 0.15),
    rgba(6, 22, 26, 0.9)
  );
  animation: cardPulse 2s ease-in-out infinite;
}

@keyframes cardPulse {
  0%,
  100% {
    box-shadow: 0 0 0 rgba(0, 255, 170, 0);
  }
  50% {
    box-shadow: 0 0 20px rgba(0, 255, 170, 0.3);
  }
}

.sportsbook-card.syncing .sportsbook-fetch-compact {
  background: rgba(0, 255, 170, 0.15);
  cursor: wait;
  pointer-events: none;
}

.sportsbook-card.syncing .sportsbook-fetch-compact .fetch-icon {
  animation: spin 1s linear infinite;
  color: #00ffaa;
}

@keyframes spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

/* ===== Action Buttons (Footer) ===== */
.sportsbook-actions-compact {
  display: flex;
  gap: 6px;
  margin-top: 8px;
  padding-top: 8px;
  border-top: 1px solid rgba(255, 255, 255, 0.05);
