/* ===== Back to Top Button - Sleek Minimalist Design ===== */
.back-to-top {
  position: fixed;
  bottom: 32px;
  right: 32px;
  width: 36px;
  height: 36px;
  border-radius: 50%;
  background: rgba(13, 27, 51, 0.6);
  border: 0.5px solid rgba(var(--color-primary-bright-rgb), 0.25);
  color: rgba(0, 255, 170, 0.7);
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", sans-serif;
  font-size: 0.75rem;
  font-weight: 300;
  cursor: pointer;
  z-index: 9999;
  display: flex;
  align-items: center;
  justify-content: center;
  box-shadow:
    0 1px 4px rgba(0, 0, 0, 0.4),
    0 0 0 0.5px rgba(0, 0, 0, 0.15) inset;
  transition:
    opacity 0.25s ease,
    transform 0.25s cubic-bezier(0.2, 0.8, 0.2, 1),
    border-color 0.2s ease,
    color 0.2s ease,
    background 0.2s ease;
  opacity: 0;
  visibility: hidden;
  transform: translateY(15px);
  outline: none;
}

.back-to-top.visible {
  opacity: 1;
  visibility: visible;
  transform: translateY(0);
}

.back-to-top:hover {
  background: rgba(0, 143, 88, 0.12);
  border-color: rgba(0, 255, 170, 0.5);
  color: rgba(0, 255, 170, 0.95);
  box-shadow:
    0 2px 8px rgba(0, 0, 0, 0.5),
    0 0 0 0.5px rgba(0, 0, 0, 0.15) inset,
    0 0 12px rgba(0, 255, 170, 0.15);
  transform: translateY(-1px);
}

.back-to-top:active {
  transform: translateY(0);
  background: rgba(0, 143, 88, 0.18);
}

