.manual-upload-content {
  display: flex;
  flex-direction: column;
  gap: 6px;
  margin-top: 6px;
  padding-top: 6px;
  border-top: 1px solid rgba(255, 255, 255, 0.05);
}

.upload-drop-zone {
  border: 1px dashed rgba(0, 214, 137, 0.3);
  border-radius: 4px;
  padding: 6px 10px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 8px;
  background: linear-gradient(145deg, rgba(0, 0, 0, 0.3), rgba(0, 30, 20, 0.2));
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  cursor: pointer;
  position: relative;
  overflow: hidden;
  min-height: 32px;
}

.upload-drop-zone::before {
  content: "☁️";
  display: block;
  font-size: 0.9rem;
  margin: 0;
  opacity: 0.7;
  filter: grayscale(100%) brightness(1.5);
  transition: all 0.3s ease;
  line-height: 1;
}

.upload-drop-zone:hover,
.upload-drop-zone.drag-over {
  border-color: var(--color-primary-bright, #00d689);
  background: linear-gradient(
    145deg,
    rgba(0, 214, 137, 0.1),
    rgba(0, 30, 20, 0.3)
  );
  box-shadow: 0 0 15px rgba(0, 214, 137, 0.1) inset;
}

.upload-drop-zone:hover::before,
.upload-drop-zone.drag-over::before {
  transform: scale(1.1);
  opacity: 1;
  filter: none;
}

.drop-zone-text {
  display: block;
  font-family: var(--sportsbook-heading-font);
  font-size: 0.65rem;
  color: #b0bec5;
  margin: 0;
  pointer-events: none;
  letter-spacing: 0.02em;
  flex: 1;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  text-align: left;
}

.hidden-file-input {
  display: none;
}

.upload-browse-btn {
  background: rgba(0, 214, 137, 0.1);
