/* ===== TABLE RESPONSIVE LAYOUT ===== */

/* Transform table to card layout on small screens */
@media (max-width: 767px) {
  .picks-table {
    display: block;
    border-collapse: collapse;
    width: 100%;
  }

  .picks-table thead {
    display: none;
  }

  .picks-table tbody {
    display: flex;
    flex-direction: column;
    gap: 12px;
  }

  .picks-table tbody tr {
    display: grid;
    grid-template-columns: 1fr auto;
    grid-template-areas:
      "pick status"
      "league league"
      "teams teams"
      "spread spread"
      "money money";
    gap: 8px;
    background: rgba(0, 0, 0, 0.3);
    border: 1px solid rgba(0, 214, 137, 0.15);
    border-radius: 8px;
    padding: 12px;
    margin-bottom: 8px;
  }

  .picks-table tbody tr td {
    padding: 8px 0;
    display: flex;
    align-items: center;
    min-height: 44px;
  }

  .picks-table tbody tr td::before {
    content: attr(data-label);
    font-weight: 700;
    font-size: 11px;
    color: rgba(0, 214, 137, 0.8);
    text-transform: uppercase;
    letter-spacing: 0.05em;
    min-width: 60px;
    margin-right: 8px;
  }

  /* Grid area assignments */
  .picks-table tbody tr td:nth-child(1) {
    grid-area: pick;
  }

  .picks-table tbody tr td:nth-child(2) {
    grid-area: league;
  }

  .picks-table tbody tr td:nth-child(3) {
