/**
 * Mobile Buttons & Controls Overcrowding Fix
 * Clean, spacious layout for all interactive elements
 *
 * Uses increased selector specificity (body prefix) instead of !important
 * to override base styles safely within media queries.
 */

/* ============================================
   FILTER TOOLBAR - COMPLETE REDESIGN
   ============================================ */

@media (max-width: 767px) {
    /* Hide the entire filter toolbar on mobile - too crowded */
    body .filter-toolbar,
    body .toolbar-layout .ft-left,
    body .toolbar-layout .ft-right {
