    .mobile-filter-toggle {
        position: fixed;
        bottom: 20px;
        right: 20px;
        width: 56px;
        height: 56px;
        border-radius: 50%;
        background: linear-gradient(135deg, #00ffaa 0%, #00d689 100%);
        border: none;
        box-shadow: 0 4px 12px rgba(0, 255, 170, 0.3);
        z-index: 500;
        display: flex;
        align-items: center;
        justify-content: center;
        cursor: pointer;
        transition: all 0.3s ease;
    }

    .mobile-filter-toggle:active {
        transform: scale(0.9);
    }

    .mobile-filter-toggle::before {
        content: '⚙';
        font-size: 24px;
        color: #081121;
